import React from 'react'
import './App.css';
import Button  from './components/Button';
import Display from './components/Display';
// import * as math from 'mathjs'

function App() {
  const [input, setInput]=React.useState("");
  const handleContent=content=>()=>{
    setInput(input + content)
    if(content==="AC")
    setInput("")
    return
    
  }
  // const handleEqual=()=>{
  //   setInput(math.eval(input))
  // }
  return (
    <div className="app">
      <div className="container">
        <Display input={input} />
        <div className="row">
          <Button handleClick={handleContent} content="AC"  type='function'/>
          <Button handleClick={handleContent} content="%" type='operator'/>
          <Button handleClick={handleContent}   content="/"  type='operator'/>
        </div>
        <div className="row">
          <Button handleClick={handleContent}  content="7"  />
          <Button handleClick={handleContent}  content="8"  />
          <Button handleClick={handleContent}  content="9"  />
          <Button handleClick={handleContent}  content="x"  type='operator'/>
        </div>
        <div className="row">
          <Button handleClick={handleContent}  content="4"  />
          <Button handleClick={handleContent}  content="5"  />
          <Button handleClick={handleContent}  content="6"  />
          <Button handleClick={handleContent}  content="-"  type='operator'/>
        </div>
        <div className="row">
          <Button handleClick={handleContent}  content="1"  />
          <Button handleClick={handleContent}  content="2"  />
          <Button handleClick={handleContent}  content="3"  />
          <Button handleClick={handleContent}  content="+"  type='operator'/>
        </div>
        <div className="row">
          <Button handleClick={handleContent}  content="0"  />
          <Button handleClick={handleContent}  content="."  />
          <Button handleClick={handleContent}  content="="  type='operator'/>
          
        </div>
      </div>
      
    </div>
  );
}

export default App;
